body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 1em;
  font-family: 'Roboto', "Arial", sans-serif;
  color: #030C17;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
}
.main__settings {
  flex-grow: 1;
  margin-right: 15px;
}
.main__flights {
  flex-grow: 4;
  margin-top: 20px;
  margin-right: 15px;
}
@media (min-width: 1200px) {
  .main {
    width: 1200px;
    margin: 0 auto;
  }
}
@media (max-width: 780px) {
  .main {
    flex-direction: column;
    width: auto;
  }
  .main__flights {
    margin-top: 35px;
  }
}
.settings {
  position: relative;
  background-color: #D9D9D9;
}
.settings__toggle {
  display: none;
}
.settings__inner {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: 45px;
  padding-left: 10px;
  background-color: #FFFFFF;
}
@media (max-width: 780px) {
  .settings__toggle {
    position: absolute;
    display: block;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
  .settings__toggle:hover .settings__svg,
  .settings__toggle:focus .settings__svg {
    fill: #E87000;
  }
  .settings__toggle:active .settings__svg {
    fill: #FF9735;
    outline: none;
  }
  .settings__inner {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}
.sorting__title {
  margin-left: 7px;
  font-size: 1.2em;
}
.sorting__list {
  padding: 0;
  list-style: none;
}
.sorting__label,
.sorting__input {
  cursor: pointer;
}
.filters__title,
.filter__title {
  margin-left: 7px;
  font-size: 1.2em;
}
.filters__list,
.filter__list {
  padding: 0;
  list-style: none;
}
.filter__inner {
  display: grid;
  grid-template-columns: 10% 90%;
}
.filter__option {
  display: flex;
  flex-direction: row;
}
.filter__label {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-grow: 2;
  vertical-align: baseline;
  cursor: pointer;
}
.filter__price {
  margin: 0 3px;
}
.filter__input {
  margin-bottom: 10px;
  padding: 2px 2px;
  cursor: pointer;
}
.flights__list {
  padding: 0;
  list-style: none;
}
.flights__empty {
  width: 100%;
  margin: 20px auto;
  text-align: center;
}
.flights__button {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 4px 80px;
  margin-bottom: 20px;
  text-align: center;
  font-size: 1.2em;
  cursor: pointer;
}
.flight {
  margin-bottom: 20px;
}
.flight__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #FFFFFF;
  background-color: #0087C9;
}
.flight__img {
  margin-left: 10px;
}
.flight__price {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  margin: 5px 10px;
}
.flight__total {
  margin: 0;
  font-size: 1.4em;
}
.flight__passengers {
  margin: 0;
}
.flight__button {
  width: 100%;
  padding: 10px 20px 8px;
  color: #FFFFFF;
  font-size: 1.2em;
  text-align: center;
  text-transform: uppercase;
  background-color: #FF9735;
  border: none;
  cursor: pointer;
}
.flight__button:hover,
.flight__button:focus {
  background-color: #E87000;
}
.flight__button:active {
  background-color: #FFB168;
  outline: none;
}
.legs {
  padding: 0;
  list-style: none;
}
.leg:not(:last-child) {
  border-bottom: 2px solid #0087C9;
}
.leg__route {
  margin-top: 8px;
  margin-left: 15px;
  margin-right: 15px;
  padding-bottom: 6px;
  border-bottom: 1px solid #E5E5E5;
}
.leg__aeroportID {
  position: relative;
  margin-left: 5px;
  color: #0087C9;
}
.leg__arrow {
  margin-left: 10px;
  margin-right: 10px;
}
.leg__time-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 6px;
  margin-left: 15px;
  margin-bottom: 4px;
}
.leg__time {
  font-size: 1.2em;
}
.leg__duration {
  position: relative;
  font-size: 1.2em;
}
.leg__duration::before {
  content: '';
  position: absolute;
  top: 4px;
  left: -20px;
  z-index: 10;
  height: 15px;
  width: 15px;
  background-image: url('../img/clock_icon.svg');
  background-size: contain;
}
.leg__date {
  color: #0087C9;
}
.leg__transfers {
  position: relative;
  margin-left: 25px;
  margin-right: 25px;
  margin-bottom: 6px;
  color: #FF9735;
  text-align: center;
}
.leg__transfers::after {
  content: '';
  position: absolute;
  top: 10px;
  right: 0;
  z-index: -10;
  height: 1px;
  width: 100%;
  background-color: #E5E5E5;
}
.leg__transfers-text {
  padding-right: 10px;
  padding-left: 10px;
  background-color: #FFFFFF;
}
.leg__carrier {
  margin-left: 15px;
  margin-bottom: 6px;
}
